.userinfo{
  width: 700px;
  background-color: #fff;
  margin: 10px 50px;
  /* padding: 10px; */
}
.userinfo>div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.userinfo>div>div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.userinfo>div>div>img{
  width: 40px;
  height: 40px;
  margin: 20px 10px 20px 10px;
}
.userinfo .info{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.userinfo .info img{
  width: 30px;
  height: 30px;
  margin-right: 10px;
  margin-left: 20px;
}
h4{
  margin: 0;
  background-color: #f6f6f6;
  line-height: 40px;
  text-indent: 20px;
}


